@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{font-size:16px}@media (max-width: 768px){html{font-size:12px}}.App{display:flex;flex-direction:column;align-items:stretch;justify-content:space-between;height:100vh;color:white;text-align:center;background-color:#272727;font-family:'Lato', sans-serif}.App *{font-family:'Lato', sans-serif}.App .App__content{flex:1 1;display:flex;justify-content:space-between;align-items:stretch;overflow:hidden}.App .App__content>*{flex:1 1}.App .Loader{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.Header{flex:0 0 auto;box-sizing:border-box;display:flex;justify-content:space-between;align-items:center;padding:0.2rem 10vw;background-color:#169cb4;color:#ffdd55;width:100%;box-shadow:0px -20px 16px 20px rgba(0,0,0,0.25)}.Header .Header__left{display:flex;justify-content:flex-start;align-items:center}.Header .Header__right{display:flex;justify-content:flex-end;align-items:center}.Header .Header__logo{position:relative;display:flex;justify-content:center;align-items:center;margin:0.3rem 0.1rem;width:2.7rem;height:2.7rem}.Header .Header__logo span{position:absolute;display:block;width:100%;height:100%;background-position:center;background-size:contain;background-repeat:no-repeat;-webkit-transform-origin:center;transform-origin:center;-webkit-transform:scale(0.9);transform:scale(0.9);transition:-webkit-transform 0.1s ease-out;transition:transform 0.1s ease-out;transition:transform 0.1s ease-out, -webkit-transform 0.1s ease-out;-webkit-filter:hue-rotate(180deg) contrast(0) brightness(100);filter:hue-rotate(180deg) contrast(0) brightness(100)}.Header .Header__logo img{width:100%;z-index:10;transition:-webkit-transform 0.1s ease-out, -webkit-filter 0.1s ease-out;transition:transform 0.1s ease-out, filter 0.1s ease-out;transition:transform 0.1s ease-out, filter 0.1s ease-out, -webkit-transform 0.1s ease-out, -webkit-filter 0.1s ease-out}.Header .Header__logo:hover span{-webkit-transform:scale(1.2) translate(-0.06rem, -0.02rem);transform:scale(1.2) translate(-0.06rem, -0.02rem)}.Header .Header__logo:hover img{-webkit-filter:brightness(1.2);filter:brightness(1.2)}.Header .Header__logo-title{height:1.6rem;margin-left:1rem}.Header .Header__logo-title img{height:100%}@media (max-width: 768px){.Header{flex-direction:column;justify-content:flex-start;align-items:stretch;padding:0}.Header .Header__left{display:flex;justify-content:center}.Header .Header__right{display:flex;justify-content:center}}

.HeaderNavigation{margin-left:auto;display:block;height:auto}.HeaderNavigation a{padding:0.6em 1.3em;border:0.1em solid transparent;background-color:transparent;color:white;font-size:1.1em;font-weight:normal;text-decoration:none;transition:background-color 0.1s ease-out}.HeaderNavigation a:not(:first-of-type){margin-left:0.5em}.HeaderNavigation a:hover{background-color:rgba(255,255,255,0.05)}.HeaderNavigation a.active{border-color:white;color:white;background-color:rgba(0,42,121,0.2);font-weight:bold}.HeaderNavigation a.primary{color:#eb5626;background-color:#ffdd55;border:1px solid #ffdd55}.HeaderNavigation a.primary:hover{background-color:#ffe57b}@media (max-width: 768px){.HeaderNavigation{flex:1 1;display:flex;justify-content:center;top:59.16px;left:0;right:0;z-index:1;margin:0;background-color:#131313}}

.Button{display:inline-block;padding:0.6em 1.3em;background-color:transparent;color:currentColor;border:1px solid currentColor;font-size:1.1em;font-weight:normal;text-decoration:none;transition:background-color 0.1s ease-out}.Button:hover{background-color:#ffffff0d}.Button.active{border-color:white;color:white;background-color:#002a7933;font-weight:bold}.Button.primary{color:#eb5626;background-color:#ffdd55;border:1px solid #ffdd55}.Button.primary:hover{background-color:#ffe57b}

.CrossCard{--CrossCard-color: #9a1616;--CrossCard-shadow: #eb5626;--CrossCard-bg: #ffdd55;margin:20px;position:relative;width:20rem;padding:2em;color:magenta;color:var(--CrossCard-color, magenta);z-index:0}.CrossCard h1{margin-top:0}.CrossCard ul{text-align:left;margin-bottom:2rem}.CrossCard::before{content:"";position:absolute;top:-4px;right:-4px;bottom:-4px;left:-4px;display:block;background-color:magenta;background-color:var(--CrossCard-shadow, magenta);-webkit-transform:rotate(2deg);transform:rotate(2deg);z-index:-1;transition:0.1s ease-out transform}.CrossCard::after{content:"";position:absolute;top:-4px;right:-4px;bottom:-4px;left:-4px;display:block;background-color:magenta;background-color:var(--CrossCard-bg, magenta);-webkit-transform:rotate(-2deg);transform:rotate(-2deg);z-index:-1;transition:0.1s ease-out transform}.CrossCard:hover::before{-webkit-transform:rotate(1deg);transform:rotate(1deg)}.CrossCard:hover::after{-webkit-transform:rotate(-1deg);transform:rotate(-1deg)}

.RoadMapPage{display:flex;flex-direction:column;align-items:center;margin:50px 0}.RoadMapPage .RoadMapPage__card{margin-top:23vh;width:80vw;max-width:30rem}

.HomePage .HomePage__banner{display:flex;justify-content:center;align-items:center;width:100%;height:40vh;background-color:#484848;background-size:cover;background-position:center}.HomePage .HomePage__cards{display:flex;justify-content:center;flex-direction:row;align-items:center;padding-top:5em}@media (max-width: 768px){.HomePage .HomePage__cards{flex-direction:column}}

.PlayerCounter p{margin:0;font-size:1.2em}.PlayerCounter p.count{margin:0.1em 0 0.7em 0;font-size:2em;font-weight:bold}

.SupportCard p{margin:0;font-size:1.2em}

